import React from "react";

// Styles
import styles from "./Disclaimer.module.css";

const Disclaimer = () => {
    return (
        <div className={styles.container}>
            <p className={styles.text}>
                Dns Ins * : Donnes Insufisants pour calculer le pourcentage de variation. Le calcul du pourcentage de
                variation se fait anne sur anne (YoY) sur la date visualise.
            </p>
            {/* <p className={styles.text}></p> */}
        </div>
    );
};

export default Disclaimer;
